

.mainPage{
  text-align: center;
  margin-top: 5rem;

}

.meals .container{
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 2rem;

}

.meals .mealImg img{
  width: 100%;
  height: auto;
  display: block;
  margin-bottom: 1.5rem;
}

.meals .mealImg .tags{
  display: inline-block;
  margin-bottom: 1rem;
}

.meals .mealVideo .container{
  display: flex;
  flex-direction: column; 
  margin-top: 0 !important; 
}
.meals .mealVideo .container h3{
  margin-bottom: 1.5rem
}
ul {
  list-style: circle inside;
  text-align: left;
  padding-left: 0;
}

.container{
  width: 70%;
  margin: 3rem auto 1rem auto;
  padding: 10px;
}
button{
  text-align: center;
  padding: 9px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  background-color: rgb(58, 192, 192);
  color: #fff;
  border-radius: 50px;
  outline: none;
  border: none;
}

@media (max-width: 767px){
  .meals .container{
    display: flex;
    flex-direction: column;    
  
  }
}